.achievements-top {
  position: relative;
  padding-top: 0;
  padding-bottom: 0;
}

.achievements-top .HeaderButton {
  position: absolute;
  top: 5px;
  left: 5px;
  z-index: 1;
}

.user-block {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-direction: column;
  flex-direction: column;
  padding-top: 24px;
  padding-bottom: 24px;
  margin-top: -44px;
}

.user-avatar {
  position: relative;
  width: 96px;
  height: 96px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border-radius: 50%;
}

.user-rating {
  position: absolute;
  padding: 3px 10px 3px 13px;
  right: -12px;
  bottom: 5px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  background: #fff;
  border-radius: 12px;
  font-size: 15px;
  color: #232324;
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.12);
          box-shadow: 0 3px 6px rgba(0, 0, 0, 0.12);
  font-weight: 500;
}

.user-rating .Icon {
  margin-right: 3px;
  margin-left: -6px;
}

.user-name {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 2px;
}

.user-name__name {
  font-size: 20px;
  line-height: 28px;
  font-weight: 500;
  color: #000;
  color: var(--text_primary);
}

.user-name__aside {
  margin-left: 4pt;
  margin-top: 3px;
}

.title-block .Header__content {
  padding-top: 14px !important;
  padding-bottom: 8px !important;
}

.type-category {
  font-size: 15px;
  line-height: 20px;
  letter-spacing: -0.24pt;
  color: #000;
  color: var(--text_primary);
  margin-bottom: 3px;
}

.register-time {
  font-size: 14px;
  line-height: 20px;
  color: #909499;
}

.achievements-box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin: -6px;
}

.achievements-box .achievement {
  max-width: calc(33.33% - 12px);
  margin: 6px;
}

.achievements-box .achievement .achievement-icon {
  max-width: 100%;
  border-radius: 50%;
  height: auto;
}

.achievements-box .Avatar {
  margin-bottom: 12px;
}

.color-blue {
  color: #3f8ae0;
}

#panel-header-mainPanel .PanelHeader__bg .PanelHeader-bg {
  background: var(--header_alternate_background);
  opacity: 0;
}

.PanelHeader__container .PanelHeader__left .PanelHeader-left-in,
.PanelHeader__container .PanelHeader__right .PanelHeader-right-in,
.PanelHeader-content.PanelHeader-content--brand {
  background: none;
}

.PanelHeader__container .PanelHeader__left .PanelHeader-left-in .Icon {
  color: var(--accent);
}

.color-write{
  color: #fff;
}

